.header {
  position: relative;

  &__conteiner {
    @include center-position;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 122px;
    width: 100%;
    padding-left: 2vw;
    padding-right: 2vw;

    @include mobile {
      min-height: 80px;
      padding-left: 5vw;
      padding-right: 5vw;
    }
  }

  &__theme {
    border: none;
    outline: none;
    height: 36px;
    width: 36px;
    background: url("../images/sun.svg") no-repeat center;
    cursor: pointer;
    transition: scale $transition-time;

    &:hover {
      scale: (1.2);
    }
  }
}
